@keyframes kf-location{
0%{transform:rotateY(0deg);-moz-transform:rotateY(0deg);-ms-transform:rotateY(0deg)
}
100%{transform:rotateY(360deg);-moz-transform:rotateY(360deg);-ms-transform:rotateY(360deg)
}
}
